// Image style
.lot-image {
  grid-row: span 4;
  padding: 0 5px;

  @media only screen and (max-width: 1200px) {
    border: none;
  }

  .lot-image-thumbs {
    max-width: 220px;
    text-align: center;
    display: block; // Changed from inline-flex to block
    overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap;
    -webkit-overflow-scrolling: touch;
    scrollbar-width: auto;
    scrollbar-color: #848484 transparent;
  
    &::-webkit-scrollbar {
      width: 12px;
      height: 20px;
    }
  
    &::-webkit-scrollbar-track {
      background: #cecece;
    }
  
    &::-webkit-scrollbar-thumb {
      background-color: #bdbdbd;
      border-radius: 6px;
      border: 3px solid #f1f1f1;
    }
  
    

    img {
      display: inline-block;
      margin: 1px;
      cursor: pointer;
      max-height: 108px;
      border: 2px solid transparent;
      transition: border-color 0.3s;

      &:hover {
        border-color: rgba(#bdbdbd, 0.5);
      }

    }
  }

  .images-count {
    text-align: center;
    margin-top: 8px;
    small {
      color: #666;
    }
  }

  .current-zoomed-image {
    margin: 0;
    padding: 0;
  }

  .no-image-placeholder{
    background: #ededed;
    text-align: center;
    padding: 20px;
    color: gray;
  }
}


